import {
  Event,
  ToolbarButtonEvent,
} from './constants';

export default ({ stateContainer, service, withNativeEvents, intents }) => {
  /**
   * @description
   * Returns the current selected `contact` from the state
   *
   * @name getSelectedContact
   * @type {function}
   * @returns {Object} contact Contact from the state
   * @inner
   */
  const getSelectedContact = stateContainer.createSelector(
    state => state.contact.data
  );

  return withNativeEvents(
    {
      [ToolbarButtonEvent.EDIT_CONTACT]: () => {
        const contact = getSelectedContact();
        intents.editContact(contact);
      },
    },
    {
      [Event.CONTACT_CREATE_DONE]: ({ contact }) => {
        service.setSelectedContact(contact);
      },
      [Event.CONTACT_UPDATE_DONE]: ({ contact }) => {
        service.setSelectedContact(contact);
      },
    }
  );
};



