import { Intent } from './constants';

/**
 * @name Intents
 * @type {Object}
 */
export default ({ createRequest, handleRequest, service }) => ({
  /**
   * @name Intents#editContact
   * @description The intent to edit the contact
   * @type {function}
   */
  editContact: createRequest(Intent.EDIT_CONTACT),

  /**
   * @name Intents#showContactDetails
   * @description Handles the intent to show the contact details
   * @type {function}
   * @param {Object} contact The contact to show.
   */
  showContactDetails: handleRequest(Intent.SHOW_CONTACT_DETAILS, contact => {
    if (contact) {
      service.setSelectedContact(contact);
    } else {
      throw new Error(`Contact is undefined. 
        Please pass a contact object as the payload of the intent`);
    }
  }),
});



