/**
 * @description
 * Helpers for ext-bbm-contact-details-ng.
 *
 * @name Helpers
 * @type {Object}
 */
export default ({ stateContainer }) => {
  /**
   * @description
   * Returns the selected `contact` from the state
   *
   * @name Helpers#getSelectedContact
   * @type {function}
   * @returns {Object} contact The current selected contact from the state
   */
  const getSelectedContact = stateContainer.createSelector(
    state => state.contact.data
  );

  /**
   * @description
   * Returns true if the preference is set to show an avatar. Otherwise returns false.
   *
   * @name Helpers#showAvatar
   * @type {function}
   * @returns {boolean}
   */
  const showAvatar = stateContainer.createSelector(
    state => Boolean(state.showAvatar)
  );

  return {
    getSelectedContact,
    showAvatar,
  };
};




