/**
 * @module ext-bbm-contact-details-ng
 *
 * @description
 * Mobile extension for a contact details view in the Contacts widget.
 *
 * @example
 * <!-- Contact widget model.xml -->
 * <property name="extension" viewHint="text-input,admin">
 *   <value type="string">ext-bbm-contact-details-ng</value>
 * </property>
 */
import uiBbAvatarKey from 'ui-bb-avatar-ng';
import i18nKey from 'ui-bb-i18n-ng';

import extEvents from './events';
import extHelpers from './helpers';
import extIntents from './intents';

export const events = extEvents;
export const helpers = extHelpers;
export const intents = extIntents;

export const dependencyKeys = [
  uiBbAvatarKey,
  i18nKey,
];




